<script src="https://cdn.jsdelivr.net/npm/vuetify@2.x/dist/vuetify.js"></script>
<script>
import axios from 'axios'
export default {
  name: 'DataManage',
  data () {
    return {
      dialog: false,
      counting: false,
      showResult: false,
      countResult: null,
      dateValid: true,
      startDate: null,
      endDate: null,
      dateRules: [
      v => !!v || '',
      v => /^[2][01]\d{2}[-][01]\d{1}[-][0-3]\d{1}$/.test(v) || ''
      ],
      menuData: [],
      input_data: {
        group: '',
        month: '',
        type: '',
        state: '',
        keyword: ''
      },
      groupofData: ['','',''],
      monthofData: ['','M3','M6','M12', 'M18', 'M24','M36','M48','M60','M72','G1','G2','G3','G4','G5','G6','G7','G8','G9','G10','G11','G12'],
      typeofData: ['','','','/'],
      releaseState: ['','',''],
      headers: [
        {
            text: '',
            align: 'start',
            sortable: false,
            value: 'title',
        },
        { text: '', value: 'state' },
        { text: '', value: 'actions', sortable: false },
        { text: '', value: 'count' }
      ],
      editedIndex: -1,
      editedItem: {
        id: '',
        title: '',
        group: '',
        month: '',
        type: '',
        state: '',
      },
      defaultItem: {
        id: '',
        title: '',
        group: '',
        month: '',
        type: '',
        state: '',
      }
    }
  },
  computed: {
    typeMenuDataForGroup(){
        if(this.input_data.group === ''){
            return this.menuData
        }else{
            return this.menuData.filter(item =>{
                return item.group === this.input_data.group
            })
        }
    },
    typeMenuDataForMonth(){
        if(this.input_data.month === ''){
            return this.typeMenuDataForGroup
        }else{
            return this.typeMenuDataForGroup.filter(item =>{
                return item.month === this.input_data.month
            })
        }
    },
    typeMenuDataForType(){
        if(this.input_data.type === ''){
            return this.typeMenuDataForMonth
        }else{
            return this.typeMenuDataForMonth.filter(item =>{
                return item.type === this.input_data.type
            })
        }
    },
    typeMenuDataForState(){
        if(this.input_data.state === ''){
            return this.typeMenuDataForType
        }else if(this.input_data.state === ''){
            return this.typeMenuDataForType.filter(item =>{
                return item.state;
            })
        }else{
            return this.typeMenuDataForType.filter(item =>{
                return !item.state;
            })
        }
    },
    titleMenuData(){
        if(this.input_data.keyword){
            return this.typeMenuDataForState.filter(item =>{
                return (
                    item.title.toLowerCase().indexOf
                        (this.input_data.keyword.toLowerCase())!==-1
                    )
            })
        }else{
            return this.typeMenuDataForState
        }
    }
  },
  watch: {
    dialog (val) {
      val || this.closeState()
    }
  },

  methods: {
    editItem (item) {
      this.editedIndex = this.menuData.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    closeState () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {
      Object.assign(this.menuData[this.editedIndex], this.editedItem)
      let data={
        DataId: this.editedItem.id,
        Release: (this.editedItem.state) ? 1 : 0
      }
      // console.log(data)
      axios.put('/api/adminApp/release',data) // .catch((err)=>{console.err(err)})
      this.closeState()
    },

    dataCount (item) {
      this.editedItem = Object.assign({}, item)
      this.counting = true
    },

    closeCount () {
      this.editedItem = Object.assign({}, this.defaultItem)
      this.startDate = null
      this.endDate = null
      this.counting = false
    },

    startCount () {
      if (this.startDate.length !== 0 && this.endDate.length !== 0){
        const config = {
          url: '/api/historyApp/surveyDownloadCount',
          method: 'get',

          params: {
            surveyId: this.editedItem.id,
            startDate: this.startDate,
            endDate: this.endDate
          }
        }
        axios(config)
          .then((res) => {
            this.countResult = res.data.data.downloadCount
            this.showResult = true
          })
          this.startDate = null
          this.endDate = null
          this.closeCount()
        }
      }
  },
  mounted () {
    axios.get('/api/adminApp/auth',{
      params: { auth: 'All_data' }
    }).then((res) => {
      let tmp_group=['','']
      let tmp_type=['/','','']
      for(let i=0;i<res.data.data.length;i++){
        let str='KIT'
        str+= (res.data.data[i].age_type==1) ? '3' : '36'
        let item={
          id: res.data.data[i].survey_id,
          title: str+res.data.data[i].wave+tmp_type[res.data.data[i].survey_type-1],
          group: tmp_group[res.data.data[i].age_type-1],
          month: res.data.data[i].wave,
          type: tmp_type[res.data.data[i].survey_type-1],
          state: res.data.data[i].release
        }
        this.menuData.push(item)
      }
    }) //.catch((err)=>{console.err(err)})
  }
}
</script>
